@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Nunito";
  src: url(./fonts/Nunito/Nunito-Medium.ttf);
}
@font-face {
  font-family: "Jua";
  src: url(./fonts/Jua/Jua-Regular.ttf);
}

.hm {
  background-color: #0f0f0f;
}

.bg {
}
.maxDiv {
  max-width: 1100px;
  margin: auto;
  border-left: 1px solid rgb(41, 41, 41);
  border-right: 1px solid rgb(41, 41, 41);
  border-bottom: 1px solid rgb(41, 41, 41);
}

/* 헤더 */

.header {
  font-family: "Nunito";
}

.logo {
  font-size: 24px;
}

/* 바디 */

.body {
  font-family: "Jua";
}

.img {
  background-image: url("https://codingapple.com/wp-content/uploads/2022/04/bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 300px;
}

.bodyLogo {
  letter-spacing: 5px;
}

.body-container {
  border-radius: 10px;
  min-width: 200px;
  height: 230px;
  background-color: white;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  position: relative;
  bottom: 0px;
  transition: all 0.25s ease-out;
}

.body-container:hover {
  bottom: 7px;
}

.container-explain {
  padding: 8px 22px 8px 22px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.4);
}

.detail-img {
}

.detail-img > img {
}

.detail-expalin {
  font-family: "Jua";
}

.start {
  opacity: 0;
}

.end {
  opacity: 1;
  transition: all 0.5s;
}

.card {
  background-color: white;
  color: black;
  border-radius: 10px;
}

.card > img {
  border-radius: 10px;
}

.navStart {
  visibility: visible;
}

.nav {
  visibility: hidden;
  transition: all 0.5s ease-in;
}

.navBg {
  background-color: #181818;
}

.footer {
}
